export default {
	data() {
		return {
			bannerList:[
				{ id:1, imgs:'../../static/banner.jpg' },
				{ id:2, imgs:'../../static/banner.jpg' }
			],//轮播图
			
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
		}
	},
	onLoad() {
		uni.showShareMenu({
			"title":"滴滴健康"
		});
	},
	
	methods: {
		toTimeBuy:function(){
			uni.navigateTo({
				url:'/pages/time/timeBuy'
			})
		},
		toInquiryAdd:function(){
			var info = uni.getStorageSync("patientInfo");
			if(!this.$util.isEmpty(info.mobile)){
				uni.showToast({
					title: '请先获取手机号',
					icon:'none',
					duration: 2000
				});
				return;
			}
			uni.navigateTo({
				url:'/pages/inquiry/inquiryAdd'
			})
		}
	}
}
